import React from 'react'

import OverlayMenu from './overlayMenu'
import Matrix from '../assets/Lobby/Matrix.webp'

// @ts-ignore
import Crawl from 'react-star-wars-crawl'

import 'react-star-wars-crawl/lib/index.css'

export default function Test() {
  return (
    <>
      <div
        style={{
          width: '100%',
          height: '200vh',
          background: '#0f3460',
          color: 'white',
          display: 'flex',
          flexDirection: 'column',
          justifyContent: 'center',
          alignItems: 'center',
          padding: '2rem',
        }}
      >
        <div
          style={{
            position: 'relative',
            width: '100%',
            maxWidth: '1200px',
          }}
        >
          <p
            style={{
              position: 'absolute',
              top: '35%',
              left: '50%',
              transform: 'translate(-50%, -50%)',
              fontSize: '2rem',
              color: '#00B8E6',
              textAlign: 'center',
              zIndex: 2,
            }}
            className="fontGoldenAge"
          >
            Qu es CNP?
          </p>

                        <Crawl
              title="Episode IV"
              subTitle="A New Hope"
              text="It is a period of civil war. Rebel spaceships, striking from a hidden base, have won their first victory against the evil Galactic Empire. During the battle, Rebel spies managed to steal secret plans to the Empires ultimate weapon, the DEATH STAR, an armored space station with enough power to destroy an entire planet. Pursued by the Empires sinister agents, Princess Leia races home aboard her starship, custodian of the stolen plans that can save her people and restore freedom to the galaxy"
            />

          <img
            src={Matrix}
            style={{
              width: '100%',
              display: 'block',
            }}
          />
        </div>
      </div>

      <OverlayMenu />
    </>
  )
}
